.TodoItem{
  display: flex;
  align-items: center;
  gap: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgb(240, 240, 240);
  /* 할일 아이템 박스 */
}

.TodoItem .checkbox_col{
  width: 20px;
  /* 체크박스감싼거 */
}

.TodoItem .title_col{
  flex: 1;
  /* 텍스트 */
}

.TodoItem .date_col{
  font-size: 14px;
  color: gray;
  /* 등록시간 박스 */
}

.TodoItem .btn_col button{
  cursor: pointer;
  color: gray;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  padding: 5px;
}